<template>
  <tas-base-crud ref="sub-module" :config="config" hide-update hide-detail>
    <template v-slot:list-table-action="data">
      <a @click="openModal('modal-rincian-bills', data)" class="btn btn-info btn-sm">Detail Rincian </a>
    </template>
    <template v-slot:list-header>
      <b-modal header-class="pb-1" v-if="modalData" id="modal-rincian-bills" hide-footer size="xl">
        <template #modal-header>
          <div class="d-flex justify-content-between flex-fill">
            <div class="d-flex flex-column flex-fill">
              <div class="text-center mb-5">
                <h5 class="mb-1 font-weight-bold">Detail Rincian Iuran BPJS Kesehatan</h5>
                <span class="font-weight-bolder">Periode {{ modalData.detail.periode_month | parse('period') }}</span>
              </div>
              <table class="table no-border mb-0">
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Tenaga Kerja</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.rel_employee_id }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Upah Tenaga Kerja</td>
                  <th class="pt-0 pr-0 nowrap-table">
                    <div class="d-flex justify-content-between">
                      <span class="mr-4">: Rp. </span>
                      <span class="text-right font-weight-bolder">{{ modalData.detail.upah_value  || 0 | parse('number') }}</span>
                    </div>
                  </th>
                </tr>
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Jabatan</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.rel_job_position_id }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Total Iuran Tambahan</td>
                  <th class="pt-0 pr-0 nowrap-table">
                    <div class="d-flex justify-content-between">
                      <span class="mr-4">: Rp. </span>
                      <span class="text-right font-weight-bolder">{{ modalData.detail.iuran_tambahan_value  || 0 | parse('number') }}</span>
                    </div>
                  </th>
                </tr>
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Status Ketenagakerjaan</td>
                  <td class="pt-0 pr-0 nowrap-table">: {{ modalData.detail.rel_status_employment_id }}</td>
                  <td class=""></td>
                  <td class="pt-0 pl-0 nowrap-table">Total Iuran Perusahaan</td>
                  <th class="pt-0 pr-0 nowrap-table">
                    <div class="d-flex justify-content-between">
                      <span class="mr-4">: Rp. </span>
                      <span class="text-right font-weight-bolder">{{ modalData.detail.iuran_company_value  || 0 | parse('number') }}</span>
                    </div>
                  </th>
                </tr>
                <tr>
                  <td colspan="3"></td>
                  <td class="pt-0 pl-0 nowrap-table">Total Iuran Tenaga Kerja</td>
                  <th class="pt-0 pr-0 nowrap-table">
                    <div class="d-flex justify-content-between">
                      <span class="mr-4">: Rp. </span>
                      <span class="text-right font-weight-bolder">{{ modalData.detail.iuran_employee_value  || 0 | parse('number') }}</span>
                    </div>
                  </th>
                </tr>
              </table>
            </div>
          </div>
        </template>
        <div class="row">
          <div class="col-12" v-if="modalData.detail">
            <h5 class="mb-4">Detail Rincian Iuran BPJS Kesehatan</h5>
            <table class="table table-custom-header v-center table-bordered">
              <tbody>
                <tr>
                  <td colspan="4" class="font-weight-bolder bg-light-primary text-uppercase">Upah Tenaga Kerja</td>
                </tr>
                <tr>
                  <th>Nama Komponen</th>
                  <th class="nowrap-table">Koefisien</th>
                  <th class="text-center">Nilai Dasar</th>
                  <th class="text-center">Nilai Komponen</th>
                </tr>
                <tr v-for="(k,i) in modalData.detail.child_data_bpjs_kes_bill_detail_upah_components" :key="i+'-komponen-iuran'">
                  <td>{{ k.rel_salary_component_id }}</td>
                  <td class="bg-light-primary">
                    <s-input v-model="k.koefisien" :item="{ type:'number', validation:[], label:null, prefix: null, groupClass:'mb-0', ic:'mr-4', api:'', attr: { precision:0 } }" />
                  </td>
                  <td class="bg-light-primary">
                    <s-input v-model="k.default_value" :item="{ type:'number', validation:[], label:null, prefix: 'Rp. ', groupClass:'mb-0', ic:'mr-4', api:'', attr: { precision:2 } }" />
                  </td>
                  <td>
                    <div class="d-flex justify-content-between">
                      <span class="">Rp.</span>
                      <span class="text-right font-weight-bold">{{ (k.koefisien * k.default_value) || 0 | parse('number') }}</span>
                    </div>
                  </td>
                </tr>
                <tr>
                  <th class="text-right" colspan="3">Upah Tenaga Kerja</th>
                  <th>
                    <div class="d-flex justify-content-between">
                      <span class="">Rp.</span>
                      <span class="text-right font-weight-bolder">{{ modalData.detail.child_data_bpjs_kes_bill_detail_upah_components.map(x => (x.koefisien * x.default_value) ).reduce((acc, curr) => acc + curr, 0) || 0 | parse('number') }}</span>
                    </div>
                  </th>
                </tr>
                <tr>
                  <td colspan="4" class="font-weight-bolder bg-light-primary text-uppercase">Iuran Tambahan</td>
                </tr>
                <tr>
                  <th colspan="3">Nama Anggota Keluarga</th>
                  <th class="text-center">Nilai Iuran</th>
                </tr>
                <template v-if="modalData.detail.child_data_bpjs_kes_bill_detail_tambahan.length">
                  <tr v-for="(t,i) in modalData.detail.child_data_bpjs_kes_bill_detail_tambahan" :key="i+'-komponen-tambahan'">
                    <td colspan="3">{{ t.rel_employee_family_id }}</td>
                    <td>
                      <div class="d-flex justify-content-between">
                        <span class="">Rp.</span>
                        <span class="text-right font-weight-bold">{{ Number(t.iuran_value) || 0 | parse('number') }}</span>
                      </div>
                    </td>
                  </tr>
                </template>
                <template v-else>
                  <td colspan="4" class="text-center bg-light-danger"> Data Keluarga tidak ditemukan. <span @click="$router.push({ name: 'tenaga-kerja', query: { view: 'detail', id: modalData.employee_id } }).catch((err) => { return err })" class="text-info font-weight-bold pointer">Klik disini</span> untuk menambahkan anggota keluarga </td>
                </template>
                <tr>
                  <th class="text-right" colspan="3">Total Iuran Tambahan</th>
                  <th>
                    <div class="d-flex justify-content-between">
                      <span class="">Rp.</span>
                      <span class="text-right font-weight-bolder">{{ modalData.detail.child_data_bpjs_kes_bill_detail_tambahan.map(x => Number(x.iuran_value) ).reduce((acc, curr) => acc + curr, 0) || 0 | parse('number') }}</span>
                    </div>
                  </th>
                </tr>
                <tr>
                  <td colspan="4" class="font-weight-bolder bg-light-primary text-uppercase">Premi BPJS Kesehatan - {{ modalData.detail.periode_month | parse('period') }} </td>
                </tr>
                <tr>
                  <th colspan="3">Premi Perusahaan (%)</th>
                  <th class="text-right">{{ modalData.detail.data_premi.prosen_premi_company | parse('prosen') }}</th>
                </tr>
                <tr>
                  <th colspan="3">Premi Tenaga Kerja (%)</th>
                  <th class="text-right">{{ modalData.detail.data_premi.prosen_premi_employee | parse('prosen') }}</th>
                </tr>
                <tr>
                  <th colspan="3">Upah Minimal</th>
                  <th>
                    <div class="d-flex justify-content-between">
                      <span class="">Rp.</span>
                      <span class="text-right font-weight-bolder">{{ Number(modalData.detail.data_premi.upah_min_value) || 0 | parse('number') }}</span>
                    </div>
                  </th>
                </tr>
                <tr>
                  <th colspan="3">Upah Maksimal</th>
                  <th>
                    <div class="d-flex justify-content-between">
                      <span class="">Rp.</span>
                      <span class="text-right font-weight-bolder">{{ Number(modalData.detail.data_premi.upah_max_value) || 0 | parse('number') }}</span>
                    </div>
                  </th>
                </tr>
              </tbody>
            </table>
            <div class="form-group mb-3 mt-0">
              <label>Deskripsi Rincian Iuran BPJS Kesehatan</label>
              <textarea v-model="modalData.detail.description" class="form-control" rows="4" placeholder="Deskripsi / note Rincian iuran simpan pinjam"></textarea>
            </div>
          </div>
        </div>
        <div class="mt-5">
          <button @click="submitData('modal-rincian-bills')" class="btn btn-primary btn-lg btn-block font-size-md">Perbarui &amp; Simpan</button>
        </div>
      </b-modal>
    </template>
  </tas-base-crud>
</template>

<script>

export default {
  name: 'crud-bpjs-kes-bill-details',
  data () {
    return {
      mainKey: 1,
      modalData: {},
      config: {
        title: 'Rincian Iuran BPJS Kesehatan',
        model_api: null,
        getter: 'bpjs-kes-bill-details',
        setter: 'bpjs-kes-bill-details',
        pk_field: 'employee_id',
        permission: {
          create: 'create-bpjs-kes-bill-details',
          read: 'view-bpjs-kes-bill-details',
          update: 'update-bpjs-kes-bill-details',
          delete: 'delete-bpjs-kes-bill-details'
        },
        custom_api: {
          list: 'custom/bpjs-kes-bill-details/list',
          create: 'custom/bpjs-kes-bill-details'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'bpjs_kes_bill_id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'koperasi_simpan_pinjam_id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'rel_job_position_id', label: 'Jabatan', methods: { list: true, detail: false, create: false, update: false, filter: false } },
          { id: 'rel_status_employment_id', label: 'Status Ketenagakerjaan', methods: { list: true, detail: false, create: false, update: false, filter: false } },
          {
            id: 'status_code',
            data: 'status_code',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { 'not_calculated_yet': 'badge badge-danger', 'calculated': 'badge badge-primary' },
                transform: 'calculated'
              },
              detail: {
                order: true,
                class: { 'not_calculated_yet': 'badge badge-danger', 'calculated': 'badge badge-primary' },
                transform: 'calculated'
              },
              create: false,
              update: {
                view_data: 'status_code',
                type: 'radio',
                value: 'calculated',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah dihitung', code: 'calculated' }, { label: 'Belum dihitung', code: 'not_calculated_yet' }]
                  }
                }
              },
              filter: {
                view_data: 'status_code',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah dihitung', code: 'calculated' }, { label: 'Belum dihitung', code: 'not_calculated_yet' }]
                  }
                }
              }
            }
          },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'upah_value', label: 'Upah Tenaga Kerja', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'iuran_company_value', label: 'Total Iuran Perusahaan', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'iuran_employee_value', label: 'Total Iuran Tenaga Kerja', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'iuran_tambahan_value', label: 'Total Iuran Tambahan', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'total_iuran_value', label: 'Total', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          {
            id: 'employee_id',
            label: 'Nama Tenaga Kerja',
            methods: {
              list: { view_data: 'rel_employee_id' },
              create: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              update: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              filter: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              }
            }
          }
        ]
      }
    }
  },
  watch: {
    mainKey: {
      immediate: false,
      deep: false,
      handler (e) {
        // console.log(this.$refs['sub-module'])
        this.$refs['sub-module'].$children[2].getData()
      }
    }
  },
  methods: {
    async openModal (id, data) {
      const detail = {
        detail: await this.getDetail('custom/bpjs-kes-bill-details/show', { id: data.rowData.id })
      }
      this.modalData = Object.assign({}, detail, data.rowData)
      this.$nextTick().then(e => {
        this.$bvModal.show(id)
      })
    },
    getDetail (api, param, obj = null) {
      return this.$_api.get(api, param).then(res => {
        return obj ? res.data[obj] || res.data : res.data
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    submitData (modalID) {
      const api = 'custom/bpjs-kes-bill-details/calculate'
      this.$_api.post(api, this.modalData.detail).then(res => {
        this.mainKey++
        this.$_alert.success(null, 'Detail Rincian berhasil diperbarui')
        this.$set(this.modalData, 'detail', res.data)
      }).catch((err) => {
        this.$_alert.error(err)
      })
    }
  }
}
</script>
<style scoped>
  .in-card-border {
    border: 2px solid;
    border-radius: 8px;
    padding: 1rem;
  }
</style>



